@font-face {
    font-family: icomoon;
    src: url(https://cdn-df.babychakra.com/font/icomoon.eot?ov6c90);
    src: url(https://cdn-df.babychakra.com/font/icomoon.eot?ov6c90#iefix) format("embedded-opentype"),url(https://cdn-df.babychakra.com/font/icomoon.ttf?ov6c90) format("truetype"),url(https://cdn-df.babychakra.com/font/icomoon.woff?ov6c90) format("woff"),url(https://cdn-df.babychakra.com/font/icomoon.svg?ov6c90#icomoon) format("svg");
    font-weight: 400;
    font-style: normal
}

[class*=" icon-"],[class^=icon-] {
    font-family: icomoon!important;
    font-style: normal;
    font-weight: 400;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

.icon-heart:before {
    content: "\e931"
}

.icon-comment:before {
    content: "\e913"
}
